/* eslint-disable no-unused-vars */
Drupal.behaviors.mainMenu = {
  attach() {
    // Variable.
    let isMobile;
    const nav = document.querySelector('#main-nav');
    const mainMenu = document.querySelector('.main-menu');
    const header = document.querySelector('.site-header');
    const buttonToggle = document.querySelector('#toggle-expand');
    const siteHeaderTop = document.querySelector('.site-header__top');
    const SiteHeaderContent = document.querySelector('.site-header__content');

    function dynamicWidth(menuItem) {
      if (!isMobile) {
        const sub = menuItem.querySelector('.main-menu--sub');
        if (sub.children && sub.children.length > 7) {
          sub.style.columnCount = 3;
        } else {
          sub.style.columnCount = 1;
        }

        const listReact = sub.getBoundingClientRect();
        const lastChildReact = sub.lastElementChild.getBoundingClientRect();
        const subWidth = lastChildReact.x + lastChildReact.width - listReact.x;
        sub.style.width = `${subWidth}px`;

        const isOverflowing = listReact.x + subWidth - window.innerWidth;
        if (isOverflowing >= -16) {
          sub.style.transform = `translateX(-${isOverflowing + 16}px)`;
        }
      }
    }

    function resetDynamicWidth(menuItem) {
      if (!isMobile) {
        const sub = menuItem.querySelector('.main-menu--sub');
        sub.style.transform = 'translateX(0)';
        sub.style.columnCount = 1;
      }
    }

    function closeSubmenu() {
      const matches = document.querySelectorAll(
        '.main-menu__link[aria-expanded="true"]',
      );
      matches.forEach((subMenu) => {
        subMenu.setAttribute('aria-expanded', false);
      });
    }

    function setAriaAttributes(menuItem, initialState = undefined) {
      const link = menuItem.querySelector('.main-menu__link');
      const linkText = menuItem.querySelector('.main-menu__link-text');
      const ariaValue = link.getAttribute('aria-expanded') === 'true';
      const isExpanded = initialState !== undefined ? initialState : ariaValue;
      const submenuLabel = menuItem.querySelector('.main-menu__submenu-label');

      if (isExpanded) {
        nav.classList.remove('main-nav--submenu-open');
        link.setAttribute('aria-expanded', false);
        submenuLabel.innerHTML = `Show submenu ${linkText.innerHTML}`;
        resetDynamicWidth(menuItem);
      } else {
        nav.classList.add('main-nav--submenu-open');
        nav.scrollTop = 0;
        link.setAttribute('aria-expanded', true);
        submenuLabel.innerHTML = `Hiiden submenu ${linkText.innerHTML}`;
        dynamicWidth(menuItem);
      }
    }

    // Add a button to return to the previous menu.
    function addKeyHandler(menuItem) {
      const { dataset } = menuItem;

      if (!dataset.jsProcessed) {
        dataset.jsProcessed = true;
        menuItem.addEventListener('keyup', (event) => {
          if (event.key === 'Escape') {
            const tog = menuItem.querySelector('.main-menu__submenu-toggle');
            setAriaAttributes(menuItem, true);
            tog.focus();
          }
        });
      }
    }

    // Add a button to return to the previous menu.
    function addBackButton(menuItem) {
      const subMenu = menuItem.parentElement.querySelector('.main-menu--sub');
      let backButton =
        subMenu && subMenu.querySelector('.main-menu__link--back');

      if (subMenu && !backButton) {
        const node = document.createElement('li');
        node.classList.add('main-menu__mobile-link');
        const data = menuItem.innerHTML || menuItem.text.trim() || '';
        const link = document.createElement('a');
        link.className = 'main-menu__link main-menu__link--back';
        link.setAttribute('href', '#');
        node.appendChild(link);
        const linkText = document.createElement('span');
        linkText.innerHTML = `Go back`;
        link.appendChild(linkText);
        subMenu.prepend(node);

        // Back button handler.
        backButton = subMenu.querySelector('.main-menu__link--back');
        backButton.addEventListener('click', (e) => {
          e.preventDefault();
          subMenu.parentElement.classList.remove('main-menu__item--open');
          menuItem.parentElement.classList.remove('expand-sub--open');
          setAriaAttributes(menuItem.parentElement);
        });
      }
    }

    function buttonSeeAll(menuItem) {
      const subMenu = menuItem.parentElement.querySelector('.main-menu--sub');
      const seeAll = subMenu && subMenu.querySelector('.main-menu__link--all');

      if (subMenu && menuItem.href && !seeAll) {
        const node = document.createElement('li');
        node.classList.add('main-menu__mobile-link');
        node.innerHTML = `<a class="main-menu__link main-menu__link--all" href="${menuItem.href}">See all</a>`;
        subMenu.prepend(node);
      }
    }

    function toggleExpanded(visibility) {
      buttonToggle.classList.toggle('toggle-expand--open', visibility);
      nav.classList.toggle('main-nav--open', visibility);
      closeSubmenu();
    }

    function onMenuItemSelected(e) {
      const target = e.currentTarget;
      const parent = target.parentElement;
      const subMenu = parent.querySelector('.main-menu--sub');

      if (subMenu) {
        e.preventDefault();
        setAriaAttributes(parent);
      }
    }

    function onMenuItemMouseEnter(e) {
      setAriaAttributes(e.currentTarget, false);
    }

    function onMenuItemMouseLeave(e) {
      setAriaAttributes(e.currentTarget, true);
    }

    function onMenuItemToggleClick(e) {
      const menuItem = e.currentTarget.parentElement;
      setAriaAttributes(menuItem);
    }

    function moveTopHeader(src, dest, element) {
      if (src && dest && element) {
        src.insertBefore(element, dest);
      }
    }

    function onMenuLinkBlur(e) {
      if (
        e.relatedTarget &&
        !e.relatedTarget.classList.contains('main-menu__link--sub-1') &&
        !e.relatedTarget.classList.contains('main-menu__submenu-toggle')
      ) {
        closeSubmenu();
      }
    }

    // Handle media query matches.
    function mediaQueryHandler(query) {
      const menuItems = nav.querySelectorAll('.main-menu__link');
      const menuItemsWithSubmenu = nav.querySelectorAll(
        '.main-menu__item--with-sub',
      );
      const subMenuToggleElements = nav.querySelectorAll(
        '.main-menu__submenu-toggle',
      );

      if (query.matches) {
        isMobile = false;
        toggleExpanded(false);
        moveTopHeader(header, SiteHeaderContent, siteHeaderTop);
        menuItemsWithSubmenu.forEach((item) => {
          item.addEventListener('mouseenter', onMenuItemMouseEnter);
          item.addEventListener('mouseleave', onMenuItemMouseLeave);
          resetDynamicWidth(item);
          addKeyHandler(item);
        });
        subMenuToggleElements.forEach((item) => {
          item.addEventListener('blur', onMenuLinkBlur);
          item.addEventListener('click', onMenuItemToggleClick);
          item.removeEventListener('click', onMenuItemSelected);
        });
        menuItems.forEach((item) => {
          item.removeEventListener('click', onMenuItemSelected);
          if (item.classList.contains('main-menu__link--sub-1')) {
            item.addEventListener('blur', onMenuLinkBlur);
          }
        });
        document
          .querySelectorAll('.main-menu__mobile-link')
          .forEach((e) => e.remove());
      } else {
        // Mobile.
        isMobile = true;
        moveTopHeader(nav, mainMenu, siteHeaderTop);
        menuItems.forEach((item) => {
          item.addEventListener('click', onMenuItemSelected);
          buttonSeeAll(item);
          addBackButton(item);

          if (item.classList.contains('main-menu__link--sub-1')) {
            item.removeEventListener('blur', onMenuLinkBlur);
          }
        });
        subMenuToggleElements.forEach((item) => {
          item.removeEventListener('blur', onMenuLinkBlur);
          item.addEventListener('click', onMenuItemSelected);
          item.removeEventListener('click', onMenuItemToggleClick);
        });
        // Remove desktop events.
        menuItemsWithSubmenu.forEach((item) => {
          item.removeEventListener('mouseenter', onMenuItemMouseEnter);
          item.removeEventListener('mouseleave', onMenuItemMouseLeave);
          const sub = item.querySelector('.main-menu--sub');
          sub.style.removeProperty('transform');
        });
      }
    }

    if (nav) {
      const { dataset } = nav;
      if (!dataset.jsProcessed) {
        dataset.jsProcessed = true;

        // Modal.
        const mediaQuery = window.matchMedia('(min-width: 769px)');
        mediaQuery.addEventListener('change', mediaQueryHandler);
        mediaQueryHandler(mediaQuery);

        // Mobile Show or Hide.
        buttonToggle.addEventListener('click', () => {
          toggleExpanded();

          if (buttonToggle.getAttribute('aria-expanded') === 'false') {
            buttonToggle.setAttribute('aria-expanded', 'true');
          } else {
            buttonToggle.setAttribute('aria-expanded', 'false');
          }
        });
      }
    }
  },
};
